@import "../../scss/_variables.scss";
@import "../../scss/_mixin.scss";
.container {
  position: fixed;
  height: $headerHeight;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  z-index: 10;
  background-color: var(--color-primary);
  border-bottom: 1px solid #333;
  transition: all 0.2s;

  &.light {
    background-color: var(--color-secondary);
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.6);
    border-bottom: 1px solid var(--color-secondary);
    transition: all 0.5s;

    .logo {
      color: var(--color-font-secondary);
    }
    .links {
      a,
      a:link,
      a:visited,
      a:active,
      a:hover {
        color: var(--color-font-secondary);
        &.active {
          color: var(--color-access-secondary);
        }
      }
    }
  }
}

.logo {
  padding-left: 2rem;
  font-size: 3rem;
  color: var(--color-font-primary);
  .heightLight {
    font-weight: bold;
    color: var(--color-hight-light);
  }
}

.menuCheckbox {
  display: none;
  &:checked {
    & + .menuButton > .hamburger {
      background: transparent;
      &::before {
        top: 0;
        transform: rotate(-45deg);
      }
      &::after {
        top: 0;
        transform: rotate(45deg);
      }
    }

    & ~ .links {
      visibility: visible;
      transform: translateY(0);
      background-color: var(--color-low-light);
    }
  }
}

.menuButton {
  display: none;
  height: 4rem;
  width: 4rem;
  margin-right: 2rem;
  cursor: pointer;
  justify-content: center;
  align-items: center;
  color: var(--color-font-primary);
  -webkit-tap-highlight-color: transparent;
  @include maxWidth(s) {
    display: flex;
  }
}

.hamburger {
  position: relative;
  &,
  &::before,
  &::after {
    width: 3rem;
    height: 0.2rem;
    border-radius: 0.1rem;
    background-color: currentColor;
  }

  &::before,
  &::after {
    content: "";
    position: absolute;
    left: 0;
    transform-origin: center center;
    transition: all 0.2s;
  }

  &::before {
    top: -1rem;
  }

  &::after {
    top: 1rem;
  }
}

.links {
  display: flex;
  gap: 2rem;
  padding-right: 3rem;
  @include maxWidth(s) {
    position: absolute;
    background-color: transparent;
    flex-direction: column;
    align-items: center;
    padding: 1rem;
    top: calc($headerHeight + 1rem);
    right: 1rem;

    visibility: hidden;
    transform: translateY(-10rem);
    transition: all 0.2s;
    a,
    a:link,
    a:visited,
    a:active,
    a:hover {
      font-size: 2.5rem;
    }
  }
}

a,
a:link,
a:visited,
a:active,
a:hover {
  font-size: 2rem;
  text-decoration: none;
  color: var(--color-font-primary);
  &.active {
    color: var(--color-access-primary);
  }
}
